@import url('https://fonts.googleapis.com/css2?family=Kode+Mono:wght@400..700&family=Poppins:wght@300;800&display=swap');

* {
    margin: 0;
    padding: 0;
}

.body-more {
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
    width: 100vw;

    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper-more {
    background-color: white;
    height: 77vh;
    width: 63vw;
    border-radius: 103px 27px;
    padding: 2rem;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
    font-style: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

.left-more,
.right-more {
    height: 90%;
    width: 50%;
}

.right-more {
    overflow: hidden;
    border-radius: 12px;
}

.right-more img {
    height: fit-content;
    width: fit-content;
}

.element-more {
    margin: 1rem;
    margin-block: 1.5rem;
}

.element-more span {
    font-weight: 700;
}

.title-more {
    text-align-last: center;
    margin-bottom: 4rem;
    width: 100%;
}